.button {
    width: 149px;
    height: 30px;
    border-radius: 3px;
    border: none;
}

.disabled {
    background: #004D99;
    opacity: 0.5;
}

.secondary {
    border: 1px solid #0066CC;
    background-color: white;
    color: #0066CC;
}

.default {
    background: #0066CC;
    border: 1px solid #0066CC;
    border-radius: 3px;
    color: white;
}

.red {
    background-color: red;
}

